<template>
<div class="small">
     {{cardLine[0].child_surname}} {{cardLine[0].child_name}}
    <line-chart   :chart-data="datacollection"></line-chart>
       <select v-on:change="vibor($event)">
    <Option v-for='item in options_info' :key="item.id"
    :item='item'
        />
        
   </select>
</div>
</template>

<script>
import LineChart from './LineChart.js'
import Option from './graphOption.vue'
//import axios from 'axios'

  export default {
    components: {
      LineChart,
      Option
    },
    props:['options_info'],
    data () {
      return {
        datacollection: null,
        cardLine:this.options_info,
      }
    },
    mounted () {
      this.fillData()

    },
    methods: {
      vibor(event){
        console.log(event.target.value);
       /* axios
      .get('/query.php?id='+ event.target.value)
      .then(response => (this.cardLine = response.data))
            .catch(error => {
        console.log(error);
      })
      .finally(() => {this.fillData()}); */
           this.cardLine= this.options_info.filter(item => {
             
            return item.rid.includes(event.target.value);
        });
        this.fillData();
      },
      fillData () {
        this.datacollection = {
          labels: [1,2,3,4,5,6],
          datasets: [
            {
              label: 'Выполненые карточки',
              backgroundColor: 'green',
              data: [this.cardLine[0].one, this.cardLine[0].two,this.cardLine[0].three,this.cardLine[0].four,this.cardLine[0].five,this.cardLine[0].six]
            }
          ],
        }
      },
      getRandomInt () {
        return Math.floor(Math.random() * (50 - 5 + 1)) + 5
      }
    }
  }
</script>
<style scoped>
  .small {
    max-width: 600px;
    margin:  20px auto;
  }
</style>